
import { defineComponent } from 'vue';
import { RouterLinkProps } from 'vue-router';

export default defineComponent({
  data() {
    return {
      boxes: [
        { id: 1, name: 'Open Movement', route: '/register-movement' },
        { id: 2, name: 'Close Movement', route: '/access-movement' },
        { id: 3, name: 'New Vehicle Brand', route: '/register-vehicleBrand' },
        { id: 4, name: 'New Vehicle Model', route: '/register-vehicleModel' },
        { id: 5, name: 'New Vehicle', route: '/register-vehicle' },
        { id: 6, name: 'New Conductor', route: '/register-conductor' },
      ],
    };
  },
  methods: {
    navigateToPage(route: string): void {
      this.$router.push(route);
      console.log('Navigating to', route);
    },
    getBoxTextTop(name: string): string {
      const parts = name.split(' ');
      return parts[0]; // Retrieves the first word from the name
    },
    getBoxTextBottom(name: string): string {
      const parts = name.split(' ');
      return parts.slice(1).join(' '); // Retrieves the remaining words and joins them with a space
    },
  },
});
